// This send the data to the ethereum blockchain

const RegisterLove = (_YourSelf, _YourPartner, _contract, _account) => {

	let msg = "Form";

	_contract.createMarriage(_YourSelf, _YourPartner, msg, {from: _account, gas: 2000000}, function (error, result) {
	  if(!error) {
	    console.log("Marriage certificate successfully recorded on Ethereum!");
	    return true;
	  } else {
	    console.log("Something have failed while registering Marriage certificate.");
	  }
	});

};

export default RegisterLove;


