import React, { Component } from "react";
import Notice from "./components/Notice";
import Output from "./components/Output";
import MarriageCertificate from "./components/MarriageCertificate";
import RegisterLove from "./components/RegisterLove";

import getWeb3 from './utils/getWeb3';
import ABI from './utils/contractInfo/ABI';
import ContractAddress from './utils/contractInfo/ContractAddress';

import Particles from 'react-particles-js';
import ParticlesConfig from './static/etherMarriage-particles.json';

const style_particle = {
  position: "fixed",
  top: 0,
  left: 0,
  width: "100%",
  height: "100%"
};

class App extends Component {

  constructor(props) {
    super(props)

    this.state = {
      web3: null,
      account: null,
      contract: null,
      msg: null
    }
  }

  //this.getData() = this.getData.bind(this);

  componentWillMount() {
    // Get we3 instance and set it as state
    getWeb3.then( web3 => {
      this.setState({
        web3: web3
      })
      console.log(this.state.web3);

    }).then( () => {
      // Get user account using Metamask, not web3 provider
      let currentAccount = window.web3.eth.accounts[0];
      this.setState({
        account: currentAccount 
      });
      console.log(this.state.account);

    }).then( () => {
      // Init contract using ABI and address
      const ContractInstance = this.state.web3.eth.contract(ABI).at(ContractAddress);
      this.setState({
        contract: ContractInstance
      });
      console.log(this.state.contract);

    })
  }

  componentDidMount() {
    // check any user-account change every second (from metamask)
    setInterval(
      window.web3.eth.getAccounts((error, accounts) => {
        if(error) return error;
        this.setState({
          account: accounts[0] 
        });
    }), 1000);
  }

  getData = (e) => {
    e.preventDefault();
    const YourSelf = e.target.elements.YourSelf.value;
    const YourPartner = e.target.elements.YourPartner.value;

    if (YourSelf && YourPartner) {
      this.setState({
        msg: "Perfect! - Please confirm transaction on metamask to register your marriage."
      });      
      // this function send data to ethereum blockchain
      RegisterLove(YourSelf, YourPartner, this.state.contract, this.state.account);

    } else {
      this.setState({
        msg: "You need to fill in both names, yours and your partner."
      });
    }
  }

  render() {
    return (
      <div>
       <Particles params={ParticlesConfig} style={style_particle} />
        <div className="box-wrapper">
          <div className="main">
            <div className="container">
              <div className="row shadow">
                <div className="col-xs-5 image-container">
                </div>
                <div className="col-xs-7 form-container">
                  <MarriageCertificate getData={this.getData} />
                  <Output 
                    msg={this.state.msg}
                  />
                  <Notice />
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

};

export default App;



