import React from "react";

const Notice = () => (
	<div>
		<h3 className="notice-container__subtitle">Wedding certificates on Ethereum</h3>
		<p className="notice-small-text"> What is this?</p>
	    <p className="notice-small-text"> Ether-marriage is Decentralized application on top of Ethereum. <br /> You can register a digital marriage, which will be accesible and immutable all over the world.</p>
		<a href="https://rinkeby.etherscan.io/address/0xac6818232b57143a01242977f84bc847e59d1107" target="_blank">Smart Contract transactions</a>
	</div>
);

export default Notice;


